.img{
  border: 1.5px solid #e67e22;
  padding: 0px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height:60%;
  width:87.8%;
  opacity: 0.85;
  margin-top: 20px;
  -webkit-transform: scale(1.15);
      -ms-transform: scale(1.15);
          transform: scale(1.15);
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  opacity: 2;

}



.Menu {
  border: 1.5px solid #e67e22;
  border-radius: 5px;
  -webkit-box-shadow: 0 2px 3px rgb(224, 224, 224);
          box-shadow: 0 2px 3px rgb(224, 224, 224);
  text-align: center;
  font-family: "Lato", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 300;
  text-rendering: optimizeLegibility;
  width: 85%;
  height: 250px;
  color: #202020;
  text-align: center;
  margin-top: 30px;
  margin-left: 20px;
  background-color: rgb(241, 237, 237);
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  opacity: 2;
  direction: rtl;
}

.Menu:hover{
  -webkit-transform: scale(.91);
      -ms-transform: scale(.91);
          transform: scale(.91);
}

.add{
  background-image: url('../../assets/images/laptop-screen-showing-latest-online-news-color-background-54520940.png');
  width: 100%;    
  min-height: 94.5vh;
  display: -ms-flexbox;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-top: -2.05%;
  
}


