.Auth {
    margin: 20px auto;
    width: 80%;
    height: 100%;
    text-align: center;
    -webkit-box-shadow: 0 2px 3px #ccc;
            box-shadow: 0 2px 3px #ccc;
    border: 1px solid #9152f8;
    padding: 10px;
    border-radius: 15px ;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background: #9152f8;
    background: -webkit-linear-gradient(top, #7579ff, #b224ef);
    background: -o-linear-gradient(top, #7579ff, #b224ef);
    background: -webkit-gradient(linear, left top, left bottom, from(#7579ff), to(#b224ef));
    background: linear-gradient(top, #7579ff, #b224ef);    
}
hr{
    width: 50%;
    margin-top: 8%;
    margin-bottom: 6%;
}
.signlogo{
    width: 70%;
}
.btn
{
    display: block;
    text-align: center;
    font-weight: 400;
    font-size: 18px;
    padding: 10px 30px;
    text-decoration: none;
    margin: auto;
    margin-bottom: 5%;
    border-radius: 10px;
    margin-top: 5%;
    -webkit-transition: background-color 0.2s, border 0.2s, color 0.2s;
    -o-transition: background-color 0.2s, border 0.2s, color 0.2s;
    transition: background-color 0.2s, border 0.2s, color 0.2s;
    background-color: #0398dd;
    color: #fff;
    border: 1px solid #0398dd;
  }
  .btn:hover,
  .btn:active {
  border: 1px solid #095274;
  background-color: #095274;
}
.Logo {
    margin: auto;
    height: 100%;
    width: 80%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
@media (min-width: 600px) {
    .Auth {
        width: 30%;
        height: 500px;
        margin-top: 10%;
    }
}

.select{
    width: 95%;
    height: 34px;
    margin-top: 4%;
    border-radius: 10px;
    text-align: center;
    color: gray;
    font: inherit;
}

.remember{
    margin-top: 25px;
    margin-left: 150px;
    color: white;
    font-size: 18px;
}
.login{
    background-image: url('../../assets/images/bg-01 (2).png');
    width: 100%;    
    min-height: 108.4vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-top: -9.05%;
    
}


